
h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}
body {
  background-color: rgb(60, 60, 60);
  color: #e2e2e2;
  font-family: 'Oxygen', sans-serif;
  /* font-family: 'Nanum Gothic', sans-serif;   */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 0;
  margin: 0;
}
button {
  background-color: rgb(70, 70, 70);
  color: white;
  border: 0;
  margin-top: 1vh;
  border-radius: 1vh;
  padding: 1vh;
}
button:hover {
  -webkit-transition: 0.5 all;
  transition: 0.5 all;
  background-color: rgb(124, 123, 123);
  color: rgb(12, 12, 12);
}
::-webkit-scrollbar {
  width: 11px;
  height: 11px;
}
::-webkit-scrollbar-thumb {
  background: #333;
  border: 1px solid #222;
  border-radius: 50px;
}
::-webkit-scrollbar-track {
  background: #666;
  border: none;
  border-radius: 53px;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app-header {
  width: 100%;
  height: 6vh;
  line-height: 6vh;
}
#app {
  max-width: 1920px;
  margin: auto;
}
.app-error {
  padding: 20vh;
}
.midi-input-select {
  max-width: 100vh;
  margin: auto !important;  
  margin-top: 2vh;
}
.midi-input-option {
  margin: 1vh;
}
input {
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid rgb(0, 243, 255);
  color: white !important;
  font-size: 1.6vh;
  padding: 1vh;
}
select {
  background-color: transparent !important;
  padding: 0.5 1vh;
  color: white !important;
  font-size: 1.6vh;
}
option {
  color: black !important;
}
